body {
  --header-height: 60px;
  margin: 0;
  font-family: Roboto, sans-serif;
  position: relative;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  width: 100%;
  min-width: 1024px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.a__logo {
  align-self: center;
  width: 40px;
  height: 40px;
  margin-left: 50px;
}

.nav {
  height: var(--header-height);
  margin-right: 50px;
  display: flex;
}

.nav__list {
  align-items: center;
  padding-left: 0;
  display: flex;
}

.nav__item {
  justify-content: center;
  align-items: center;
  margin: 0 0 0 20px;
  list-style: none;
  display: flex;
  position: relative;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  height: var(--header-height);
  align-items: center;
  font-size: 12px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

